body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}


.btn {
  box-sizing: border-box;
  background: #fff;
  border: 1px solid rgba(153,153,153,1);
  border-radius: 4px;
  padding: 4px 1em;
  margin: 5px;
  position: relative;
  outline: none;
  overflow: hidden;
}

.content{
  position: relative;
  z-index: 1;
}

.circle {
  position: absolute;
  background: rgb(5, 214, 144);
  border-radius: 50%;
  width: 10px;
  height: 10px;
  display: inline-block;
  -webkit-animation: 1s big forwards;
          animation: 1s big forwards;
  z-index: 0;
  opacity: .5;
}

@-webkit-keyframes big{
  0% {
    -webkit-transform: scale(0.1);
            transform: scale(0.1);
  }
  100% {
    -webkit-transform: scale(10);
            transform: scale(10);
  }
}

@keyframes big{
  0% {
    -webkit-transform: scale(0.1);
            transform: scale(0.1);
  }
  100% {
    -webkit-transform: scale(10);
            transform: scale(10);
  }
}
